export const evaluatePosition = async (fen) => {
  try {
    const response = await fetch('http://127.0.0.1:5000/api/evaluate', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ fen }),
    });
    const data = await response.json();
    return data.evaluation;
  } catch (error) {
    console.error('Error evaluating position:', error);
    return null;
  }
};

export const getBestMove = async (fen) => {
  try {
    const response = await fetch('http://127.0.0.1:5000/api/bestmove', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ fen }),
    });
    const data = await response.json();
    
    // Formater le meilleur coup
    if (data.best_move) {
      const move = data.best_move;
      const piece = move.charAt(0).toUpperCase(); // Prendre la première lettre (P pour pawn, N pour knight, etc.)
      const from = move.substring(0, 2).toUpperCase(); // Prendre les deux caractères suivants
      const to = move.substring(2, 4).toUpperCase(); // Prendre les deux derniers caractères
      
      // Remplacer les lettres par les noms des pièces
      const pieceNames = {
        'P': 'Pion',
        'N': 'Cavalier',
        'B': 'Fou',
        'R': 'Tour',
        'Q': 'Dame',
        'K': 'Roi'
      };
      
      return `${from}:${to}`;
    }
    return null;
  } catch (error) {
    console.error('Error getting best move:', error);
    return null;
  }
};
