import React, { useState,useEffect,useRef } from 'react';
import { Chessboard } from 'react-chessboard';
import { onPieceClick, onPieceDrop } from '../utils/MoveHandler';
import { createNewGame } from '../utils/GameState';
import { Chess } from 'chess.js';
import MoveRecapComponents from './MoveRecapComponents';
import GameStatus from './GameStatusComponents';
import { evaluatePosition, getBestMove } from '../services/stockfishService';
import '../styles/evaluation-panel.css';

const ChessboardComponent = ({ socket, gameId, playerColor, boardSizePx }) => {
  const [isAIActive, setIsAIActive] = useState(false);
  const [difficulty, setDifficulty] = useState('medium');

  const handleAIToggle = () => {
    setIsAIActive(!isAIActive);
  };

  const handleDifficultyChange = (e) => {
    setDifficulty(e.target.value);
  };
  
  const [game, setGame] = useState(createNewGame());
  const [squareStyles, setSquareStyles] = useState({});
  const [selectedSquare, setSelectedSquare] = useState(null);
  const [possibleMoves, setPossibleMoves] = useState([]);
  const [gameStatus, setGameStatus] = useState("En cours: Au blanc de jouer");
  const [isGameOver, setIsGameOver] = useState(false);
  const [moves,setMoves]=useState([]);
  const [evaluation, setEvaluation] = useState(null);
  const [bestMove, setBestMove] = useState(null);





  const currentGameIdRef = useRef(gameId);  // Permet d’accéder au gameId sans redéclencher useEffect
  const gameRef = useRef(game);             // Permet d’accéder à la game actuelle sans stale state

  useEffect(() => {
    currentGameIdRef.current = gameId;
  }, [gameId]);

  useEffect(() => {
    gameRef.current = game;

    // Évaluer la position et obtenir le meilleur coup
    const evaluateCurrentPosition = async () => {
      if (!game) return;
      
      try {
        const fen = game.fen();
        const evaluation = await evaluatePosition(fen);
        const bestMove = await getBestMove(fen);
        
        setEvaluation(evaluation);
        setBestMove(bestMove);
      } catch (error) {
        console.error('Erreur lors de l\'évaluation:', error);
      }
    };

    evaluateCurrentPosition();
  }, [game]);

  useEffect(() => {
    if (!socket) return;

    const handleMessage = (event) => {
      try {
        const data = JSON.parse(event.data);

        // 🟢 Nouveau jeu reçu du serveur
        if (data.type === 'gameStart') {
          setGame(new Chess());
          setMoves([]);
          setGameStatus("En cours: Au blanc de jouer");
          setIsGameOver(false);
          return;
        }

        // 🔄 Coup reçu
        if (data.type === 'move' && data.gameId === currentGameIdRef.current) {
          const gameCopy = new Chess(gameRef.current.fen());
          const { from, to, promotion } = data.move;
          const move = gameCopy.move({ from, to, promotion });

          if (!move) {
            console.error("❌ Coup invalide :", data.move);
            return;
          }

          setGame(gameCopy);
          setMoves((prev) => [...prev, `${move.piece}.${move.from} -> ${move.piece}.${move.to}`]);
          setGameStatus(
            gameCopy.isCheckmate()
              ? 'Échec et mat'
              : gameCopy.turn() === 'w'
                ? 'En cours: Au blanc de jouer'
                : 'En cours: Au noir de jouer'
          );
        }

        // 🚩 Fin de partie
        if (data.type === "gameOver") {
          console.log("📩 Réception de gameOver :", data);
          setGameStatus(data.winner);
          setIsGameOver(true);
          setGame(new Chess());
          setMoves([]);
        }
      } catch (error) {
        console.error("Erreur WebSocket :", error);
      }
    };

    // 🎯 Attache le listener
    socket.addEventListener("message", handleMessage);

    return () => {
      // 🧹 Nettoyage lors d’un changement de socket (ou unmount du composant)
      socket.removeEventListener("message", handleMessage);
    };
  }, [socket]); 


  const handleReset = (isAbandon = false) => {
    if (isAbandon) {
      socket.send(JSON.stringify({ 
        type: "abandon", 
        gameId, 
        playerColor 
      }));
    }
     else {
      setGameStatus("En cours: Au blanc de jouer");
    }
    if (!isAbandon) {
      setGame(new Chess()); // Réinitialiser seulement en cas de reset, pas d'abandon !
    }
    setIsGameOver(isAbandon); // Si abandon => partie terminée
    setMoves([]);
  };
  

  return (
    <main className="game-section">
      {/* Panel évaluation */}
      <aside className="evaluation-panel">
        <h3>Évaluation de la position</h3>
        <div className="info-item">
          <label>Situation</label>
          <span className="value">
            {evaluation !== null
              ? (evaluation >= 0 ? 'Avantage des blancs' : 'Avantage des noirs')
              : 'Calcul en cours...'}
          </span>
        </div>
        <div className="info-item">
          <label>Score</label>
          <span className="score-value">
            {evaluation !== null ? Math.abs(evaluation).toFixed(2) : 'Calcul en cours...'}
            <span className={evaluation !== null ? (evaluation >= 0 ? 'score-positive' : 'score-negative') : ''}>
              {evaluation !== null ? (evaluation >= 0 ? ' (+)' : ' (-)') : ''}
            </span>
          </span>
        </div>
        <div className="info-item">
          <label>Meilleur coup</label>
          <span className="move-description">
            {bestMove ? bestMove : 'Calcul en cours...'}
          </span>
        </div>
        <div className="info-item">
          <label>Status</label>
          <span className="move-description">
            {gameStatus}
          </span>
        </div>
      </aside>

      {/* Échiquier + panneau IA */}
      <section className="chessboard-container" style={{flex: 1, minWidth: 0}}>
        <div className="ia-panel-content">
          <div className="ia-settings">
            <div className="ia-toggle">
              <label>IA Activée</label>
              <input type="checkbox" checked={isAIActive} onChange={handleAIToggle} />
            </div>
            <div className="difficulty-settings">
              <label>Difficulté</label>
              <select value={difficulty} onChange={handleDifficultyChange}>
                <option value="easy">Facile</option>
                <option value="medium">Moyen</option>
                <option value="hard">Difficile</option>
              </select>
            </div>
          </div>
        </div>
        <div className="chess_board">
          <Chessboard 
            key={game}
            position={game.fen()}
            boardWidth={boardSizePx}
            customBoardStyle={{
              background: '#f0d9b5',
              borderRadius: '0.8vw',
              boxShadow: '0 0.4vw 0.6vw rgba(0,0,0,0.1)',
              transition: 'all 0.3s ease'
            }}
            customSquareStyle={{
              background: '#f0d9b5',
              borderRadius: '0.8vw',
              transition: 'all 0.3s ease'
            }}
            onSquareClick={(square) => 
              !isGameOver && onPieceClick(game, square, setSquareStyles, setSelectedSquare, selectedSquare, possibleMoves, setPossibleMoves, setGame, setGameStatus, setIsGameOver, setMoves, moves, socket, gameId, playerColor)
            }
            onPieceDrop={(sourceSquare, targetSquare) => 
              !isGameOver && onPieceDrop(game, sourceSquare, targetSquare, setGame, setSquareStyles, setGameStatus, setIsGameOver, setMoves, moves, socket, gameId, playerColor)
            }
            customSquareStyles={squareStyles}
            className={isGameOver ? 'chessboard-disabled' : ''}
          />
        </div>
      </section>

      {/* Panel historique dynamique */}
      <aside className="recap-container">
        <MoveRecapComponents move={moves} />
      </aside>
    </main>
  );
};

// On exporte aussi les données dynamiques pour HomePage
export default ChessboardComponent;
